<template>
  <div>
    <mrc_ess_table v-if="$route.name=='jobs_manage'"
                   :pageName='pageName'
                   :headers='headers'
                   :paginationOptions='options'
                   @turnPage='getlist'
                   :isLoading='loading'
                   :slotKey="['job_post_name','dept','is_used']"
                   :items='desserts'>
      <template v-slot:top>
        搜索框
      </template>
      <template v-slot:item.job_post_name="{item}">
        <span class="ft1">{{item.job_post_name}}</span><br />
        <span class="chipbox">
          <v-chip class="ml-2"
                  style="margin-top:-15px;margin-left:-5px"
                  v-for="(item1,index1) in item.job_post_grades"
                  :key="index1">
            {{item1}}
          </v-chip>
        </span>
      </template>
      <!-- 所属部门及单位 -->
      <template v-slot:item.dept="{item}">
        <div style="margin-bottom:12px;"> {{item.dept_name}}</div>
        <div class="dept_all"> {{item.dept_all}}</div>
      </template>

      <!-- 状态 -->
      <template #item.is_used="{item}">
        <v-btn rounded
               color="green"
               dark
               x-small
               v-if="item.is_used==1">{{$t('label_To_enable_the')}}</v-btn>
        <v-btn rounded
               color="red"
               dark
               x-small
               v-else>{{$t('label_disable')}}</v-btn>
      </template>

      <!-- 操作 -->
      <template #item.actions='{item}'>
        <menu-list :list='menulist'
                   @handleClick='handleClick($event,item)'>
          <template v-slot:showAction>
            <v-icon>mdi-dots-vertical</v-icon>
          </template>
        </menu-list>
      </template>

    </mrc_ess_table>
    <template v-else>
      <router-view></router-view>
    </template>
  </div>
</template> 

<script>
import { getPostList, deletePost } from '@/service/api/postmanagement.js'
export default {
  components: {
  },
  props: {},
  computed: {
    headers () {
      return [
        {
          text: this.$t('label_Post_and_Grade'),
          align: 'start',
          sortable: false,
          value: 'job_post_name',
          width: '400'
        },
        {
          text: this.$t('label_Department_and_unit'),
          align: 'start',
          sortable: false,
          value: 'dept',
          width: '400'
        },
        {
          text: this.$t('label_Number_of_employees'),
          align: 'start',
          sortable: false,
          value: 'staff_number',
        },
        {
          text: this.$t('label_Whether_to_enable'),
          align: 'start',
          sortable: false,
          value: 'is_used',
        },
        {
          text: '',
          value: 'actions',
          sortable: false,
          align: 'start',
        },
      ]
    },
    menulist () {
      return [
        { name: this.$t('label_View') },
        { name: this.$t('label_Edit') },
        { name: this.$t('label_delete') },
      ]
    }
  },
  data () {
    return {
      loading: false,
      pageName: 'new_positions',
      options: {
        pagesize: 5,//条数
        cpage: this.$store.state.cpage,  //页数
        totalCount: null, //总条数
        totalpage: null, //总页数
      },
      desserts: [],
      crsj: []
    };
  },
  created () { },
  async mounted () {
    await this.getlist()
  },
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  },
  methods: {
    turnPage ({ cpage, pagesize }) {
      this.options.cpage = cpage
      this.options.pagesize = pagesize
      this.getlist()
    },
    addnewpositions () {
      this.$store.commit('set_subPage_name', '')
      this.$store.commit('set_uuid', '')
      this.$store.commit('set_iconstatus', '')  //行内进图标显示
      this.$router.push({
        name: 'new_positions'
      })
    },
    async getlist (obj) {  //获取岗位列表 
      this.loading = true
      const org_level_uuid = this.$store.state.currentCompany
      if (this.desserts.length == 1 && this.options.cpage != 1) this.options.cpage--
      if (obj) {
        const { cpage, pagesize } = obj
        this.options = obj
      }
      const res = await getPostList({
        cpage: this.options.cpage,
        pagesize: this.options.pagesize,
        org_level_uuid
      })
      if (res.code == 200) {
        this.options.totalCount = res.totalCount  //总条数
        this.options.totalpage = res.totalpage     //总页数
        this.desserts = res.data.map(item => {
          item.job_post_grades = (item.job_post_grades.indexOf(',') != -1) ? item.job_post_grades.split(',') : [item.job_post_grades];
          return item
        })
      }
      this.loading = false
    },
    godetail (item) {  //行内进编辑详情页
      this.$store.commit('set_uuid', item.uuid)
      this.$store.commit('set_iconstatus', true)  //行内进图标显示
      this.$router.push({
        name: 'detail_positions',
        params: {
          uuid: item.uuid,
          iconstatus: true
        }
      })
    },
    // 编辑
    editPrompt (item) { //详情页 
      this.$store.commit('set_uuid', item.uuid)
      this.$store.commit('set_iconstatus', false)  //行内进图标显示
      this.$router.push({
        name: 'detail_positions',
        params: {
          uuid: item.uuid,
          iconstatus: false
        }
      })
    },
    // 删除
    handleDel (item) {
      this.$myConfirm({
        content: '确认删除客户？'
      }).then(() => {
        // if (this.desserts.length == 1) this.options.cpage--
        deletePost({ uuid: item.uuid }).then(res => {
          if (res.code == 200) {
            if (this.desserts.length == 1) this.options.cpage--
            this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
            this.getlist()
          }
          else {
            this.$store.commit('newsTips', res.msg)
          }
        })
      }).catch(() => {
        // console.log(22);
      })
    },
    handleClick ({ index }, item) {
      switch (index) {
        case 0:
          this.godetail(item)
          break
        case 1:
          this.editPrompt(item)
          break
        case 2:
          this.handleDel(item)
          break
      }
    }
  },
  watch: {
    $route (val) {  //监听路由变化
      // console.log(this.$route.path);
      if (this.$route.name == 'jobs_manage') {
        // console.log('更新list');
        this.getlist()
      }
    }
  },
};
</script>

<style scoped>
.ft1 {
  font-size: 16px;
  text-align: left;
  display: inline-block;
  margin-top: 5px;
}
.chipbox {
  display: flex;
  padding-top: 15px;
  margin-bottom: 5px;
  max-width: 400px;
  height: 40px;
  line-height: 40px;
  align-items: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.dept_all {
  color: #aaaaaa;
  width: 400px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>
